#EditItemContainer{
    /* height: 70vh; */
    /* margin:10vh 0; */
    /* position: absolute; */
    padding: 40px;
    left: 25vw;
    min-width: 50vw;
    background: rgb(208,228,247); /* Old browsers */ /* FF3.6-15 */
    background: -webkit-linear-gradient(top, rgba(208,228,247,1) 0%,rgba(115,177,231,1) 24%,rgba(10,119,213,1) 50%,rgba(83,159,225,1) 79%,rgba(135,188,234,1) 100%); /* Chrome10-25,Safari5.1-6 */
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(208,228,247,1)),color-stop(24%, rgba(115,177,231,1)),color-stop(50%, rgba(10,119,213,1)),color-stop(79%, rgba(83,159,225,1)),to(rgba(135,188,234,1)));
    background: -o-linear-gradient(top, rgba(208,228,247,1) 0%,rgba(115,177,231,1) 24%,rgba(10,119,213,1) 50%,rgba(83,159,225,1) 79%,rgba(135,188,234,1) 100%);
    background: linear-gradient(to bottom, rgba(208,228,247,1) 0%,rgba(115,177,231,1) 24%,rgba(10,119,213,1) 50%,rgba(83,159,225,1) 79%,rgba(135,188,234,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#d0e4f7', endColorstr='#87bcea',GradientType=0 ); /* IE6-9 */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#959595', endColorstr='#1b1b1b',GradientType=1 ); /* IE6-9 */;
    z-index: 50;
    font-size: 1.3em;
    border-radius: 5px;
    -webkit-box-shadow: 10px 10px 50px 0px black;
            box-shadow: 10px 10px 50px 0px black;
}

#EditItemContainer input{
    width: 99%;
    font-size: 1.1em;
    margin: 10px 0 30px;
    border-radius: 5px;
}
#EditItemContainer h1{
    margin: 10px
}
#EditItemContainer img{
    /* margin: 0 auto; */
}

#close {
    background-color: rgb(170, 213, 250);
    color: rgb(0, 0, 0);
    font-size: 1em;
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 3px 6px;
    border-radius: 4px;
}
#EditItemContainer label{
    margin: 50px;
    
}

input#editItemCompleted{
    height:40px;
    width:40px;
    margin-right: 15px;
    position: relative;
    bottom: -5px;
}

button{
    padding: 10px;
    margin-right: 15px;
}

@media screen and (min-width: 1200px){
    #EditItemContainer{
        width: 50vh;
        margin: 0 auto;
    }
}


